.App {
  text-align: center;
}

.flex-container {
    display: flex;
}

.flex-child {
    flex: 1;
}  

.button {
  background-color: #00C65E;
  border: none;
  padding: 5px;
  color: #0D0D0D;
  border-radius: 5px 5px 5px 5px;
}

.input-field {
  background-color: #EBEFEE;
  color: #0D0D0D;
  border: none;
  font-size: 14px;
  padding: 5px;
  border-radius: 5px 5px 5px 5px;
}

.flex-child:first-child {
    margin-right: 20px;
} 

.App-header {
  background-color: #0D0D0D;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: #EBEFEE;
}

::placeholder {
  color: #0D0D0D;
  opacity: 1;
}

:-ms-input-placeholder {
  color: #0D0D0D;
}

::-ms-input-placeholder {
  color: #0D0D0D;
}